h1 {
  color: #fe6637;
}

ul {
  max-width: 300px;
  padding: 10px;
  border: 1px solid;
  background-color: rgb(248, 247, 237);
}

.listWrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
  font-size: 15px;
}

li {
  font-size: 20px;
  color:seagreen;
  text-align: center;
  list-style: none;
}

.hover {
  display: none;
}

.block {
  display: block;
  padding: 5px 10px;
  margin: 5px 10px;
  position: relative;
  overflow: hidden;
  border: 2px solid #fe6637;
  border-radius: 8px;
  color: #fe6637;
  transition: .2s ease-in-out;
  outline: none;
}

.block:before {
  content: "";
  background: linear-gradient(
    90deg, rgba(255, 255, 255, .1), rgba(255, 255, 255, .5)
    );
  height: 50px;
  width: 50px;
  position: absolute;
  top: -8px;
  left: -75px;
  transform: skewX(-45deg);
}
.block:hover {
  background: #fe6637;
  color: #fff;
}
.block:hover:before {
  left: 150px;
  transition: .5s ease-in-out;
}

.selected {
  font-weight: bold;
}

.notSelected {
  font-weight: normal;
}

.reset {
  background-color: #4CAF50;
  outline: none;
}


